import {Component, OnInit} from '@angular/core';
import {Storage} from "@ionic/storage";
import {Router} from "@angular/router";
import {EventProviderService} from "../../services/event-provider.service";
import {CommonService} from "../../services/common.service";
import { NavController } from '@ionic/angular';
import { AdMobPro, AdMobOptions } from '@ionic-native/admob-pro/ngx';

// import {Events} from "@ionic/angular";

@Component({
    selector: 'app-home-tabs',
    templateUrl: './home-tabs.page.html',
    styleUrls: ['./home-tabs.page.scss'],
})
export class HomeTabsPage implements OnInit {

    enable_home = true;
    ch_id: any;
    current_page = 'home';
    home_tabs = [{
        name: 'Home',
        status: 'false',
        page_name: 'home',
        page_id: '',
        full_link: ''

    }, {
        name: 'Feed',
        status: 'false',
        page_name: 'Feed',
        page_id: '',
        full_link: 'feed-tab'
    }, {
        name: 'Chat',
        status: 'false',
        page_name: 'Chat',
        page_id: '',
        full_link: 'chat'
    }];
    activeHome: boolean = false;

    constructor(
        private storage: Storage,
        private router: Router,
        // public events: Events,
        private event_provider: EventProviderService,
        private common: CommonService,
        private nav: NavController,
        private admobPro: AdMobPro
    ) {

        const bannerConfig: AdMobOptions = {
            // add your config here
            // for the sake of this example we will just use the test config
            adId:'ca-app-pub-6533902948762053/1398181345',
            isTesting: true,
            autoShow: true
        };
        this.admobPro.createBanner(bannerConfig).then(() => {
            console.log('BANNER LOADED');
            this.admobPro.showBanner(this.admobPro.AD_POSITION.TOP_CENTER);
        }).catch(e => console.log('error: ',e));
    }

    ngOnInit() {
        this.checkUrl();
        this.getChurch();
        // this.nav.
        
        this.event_provider.getAppTheme.subscribe((res: any) => {
            this.ch_id = res.ch_id;
        })
        // event subscription to update tabs
        this.event_provider.updateHomeTabs.subscribe(value => {              // listening event
           this.home_tabs = JSON.parse(value.value);
        });

        this.event_provider.isHomeEnable.subscribe(value => {              // listening event
            this.enable_home = value.value.is_enable_menu || value.value.is_enable;
        });

        if (this.enable_home) {
            // updating home page status from variable set in common service
            // "enable_home" variable is used to switch ON or OFF the back button on Mobile View
            // If the HOME page is disabled and THIS page is selected as first landing page, there should be no BACK button
            this.enable_home = this.common.is_home_enable.is_enable || this.common.is_home_enable.is_enable_menu;
        }
    }

    ionViewDidEnter(){
        const url = this.router.url;
        const urlParts = url.split('/');
        if(urlParts[2] === 'home'){
            this.activeHome = true;
        }
    }

    ionViewDidLeave(){
        this.activeHome = false;
    }

    public onOpenItem() {
        this.activeHome = false;
        // this.router.navigate([`home-tabs/home/${this.ch_id}`]);
      }

      onOpenHome(){
          this.activeHome = true;
        this.router.navigate([`home-tabs/home/${this.ch_id}`]);
      }

    getChurch() {
        this.storage.get('theme').then(theme => {
            if (theme) {
                this.ch_id = theme.ch_id;
                if (theme.home_tabs) {
                   this.home_tabs = JSON.parse(theme.home_tabs);
                    // this.updateHomeTabs();
                }
            }
        })
    }

    updateHomeTabs(){
        let body = JSON.stringify({id: this.ch_id});
        this.event_provider.getChurchData.subscribe(async (res: any) =>{
            if (res){

            }
        });
    }

    checkUrl() {
        let url: string = this.router.url;
        if (url.includes("home-tabs/home/")) {
            this.current_page = 'home';
        } else {
            this.current_page = '';
        }
    }

}
